/**
 * ============================================================
 * PGINA DE REGISTRO (app/register/page.tsx)
 * ============================================================
 * Formulario de registro de nuevos usuarios.
 * Incluye validaci贸n de campos y selecci贸n de rol.
 */

"use client"

import type React from "react"

import { useState } from "react"
import { useRouter } from "next/navigation"
import Link from "next/link"
import { Eye, EyeOff, FileEdit, Moon, Sun, Sparkles, Users, BarChart3, Zap } from "lucide-react"
import { AnimatedBackground } from "@/components/auth/animated-background"
import { FloatingShapes } from "@/components/auth/floating-shapes"
import { useAuth } from "@/lib/auth"
import { toast } from "sonner"

/** P谩gina de registro de usuarios */
export default function RegisterPage() {
    const router = useRouter()
    const { register } = useAuth()

    const [formData, setFormData] = useState({
        username: "",
        nombre: "",
        email: "",
        password: "",
        numeroEquipo: "",
    })
    const [showPassword, setShowPassword] = useState(false)
    const [isLoading, setIsLoading] = useState(false)
    const [theme, setTheme] = useState<"light" | "dark">("light")

    const handleSubmit = async (e: React.FormEvent) => {
        e.preventDefault()

        if (!formData.nombre || !formData.email || !formData.password) {
            toast.error('Por favor completa todos los campos obligatorios')
            return
        }

        setIsLoading(true)

        try {
            await register({
                username: formData.username || formData.email, // Use email as username if not provided
                nombre: formData.nombre,
                email: formData.email,
                password: formData.password,
                numeroEquipo: formData.numeroEquipo || undefined,
            })
            toast.success('隆Registro exitoso! Por favor inicia sesi贸n.')
            router.push('/login')
        } catch (error: any) {
            console.error('Register error:', error)
            toast.error(error.message || 'Error al registrarse')
        } finally {
            setIsLoading(false)
        }
    }

    const toggleTheme = () => {
        setTheme(theme === "light" ? "dark" : "light")
    }

    return (
        <div
            className={`min-h-screen relative overflow-hidden transition-colors duration-500 ${theme === "dark"
                    ? "bg-gradient-to-br from-[#0f0a1e] via-[#1a1333] to-[#0d1526]"
                    : "bg-gradient-to-br from-[#f8f9fc] via-[#eef1f8] to-[#e8f4f8]"
                }`}
        >
            <AnimatedBackground theme={theme} />
            <FloatingShapes theme={theme} />

            {/* Theme toggle */}
            <button
                onClick={toggleTheme}
                className={`fixed top-4 right-4 p-2.5 rounded-full transition-all duration-300 hover:scale-110 z-50 ${theme === "dark" ? "bg-white/10 hover:bg-white/20 text-white" : "bg-black/5 hover:bg-black/10 text-gray-700"
                    }`}
            >
                {theme === "dark" ? <Sun className="w-5 h-5" /> : <Moon className="w-5 h-5" />}
            </button>

            {/* Main content */}
            <div className="relative z-10 min-h-screen flex items-center justify-center p-4">
                <div className="w-full max-w-5xl flex flex-col lg:flex-row gap-6 lg:gap-8">
                    {/* Left card - Info */}
                    <div
                        className={`flex-1 rounded-3xl p-8 lg:p-10 backdrop-blur-xl transition-all duration-500 animate-slide-in-left ${theme === "dark"
                                ? "bg-white/5 border border-white/10 shadow-2xl shadow-purple-500/10"
                                : "bg-white/70 border border-white/50 shadow-xl shadow-gray-200/50"
                            }`}
                    >
                        <div className="flex items-center gap-3 mb-6">
                            <div className="w-12 h-12 rounded-xl bg-gradient-to-br from-[#F4C04A] to-[#ff9500] flex items-center justify-center">
                                <FileEdit className="w-6 h-6 text-white" />
                            </div>
                            <div>
                                <h1 className={`text-xl font-bold ${theme === "dark" ? "text-white" : "text-gray-900"}`}>
                                    Dann+Salud Online
                                </h1>
                                <p className={`text-sm ${theme === "dark" ? "text-gray-400" : "text-gray-500"}`}>Crea tu cuenta</p>
                            </div>
                        </div>

                        <h2
                            className={`text-2xl lg:text-3xl font-bold mb-6 leading-tight ${theme === "dark" ? "text-white" : "text-gray-900"
                                }`}
                        >
                            Desbloquea{" "}
                            <span className="bg-gradient-to-r from-[#C62FA8] to-[#0E6FFF] bg-clip-text text-transparent">
                                campa帽as masivas
                            </span>{" "}
                            y auto-respuestas inteligentes.
                        </h2>

                        <ul className="space-y-4">
                            {[
                                { icon: Users, text: "Organiza tus campa帽as y contactos desde un 煤nico lugar.", color: "#17C787" },
                                { icon: Zap, text: "Conecta tu WhatsApp con sesi贸n dedicada.", color: "#1E88E5" },
                                { icon: BarChart3, text: "Automatiza respuestas y mide resultados.", color: "#C62FA8" },
                            ].map((item, index) => (
                                <li
                                    key={index}
                                    className="flex items-start gap-3 animate-fade-in-up"
                                    style={{ animationDelay: `${0.2 + index * 0.1}s` }}
                                >
                                    <div
                                        className={`w-8 h-8 rounded-lg flex items-center justify-center flex-shrink-0 ${theme === "dark" ? "bg-white/10" : "bg-gray-100"
                                            }`}
                                    >
                                        <item.icon className="w-4 h-4" style={{ color: item.color }} />
                                    </div>
                                    <span className={`text-sm lg:text-base ${theme === "dark" ? "text-gray-300" : "text-gray-600"}`}>
                                        {item.text}
                                    </span>
                                </li>
                            ))}
                        </ul>

                        <div className="mt-8 flex items-center gap-2">
                            <Sparkles className="w-5 h-5 animate-pulse text-[#F4C04A]" />
                            <span className={`text-xs ${theme === "dark" ? "text-gray-500" : "text-gray-400"}`}>
                                Comienza gratis, escala cuando lo necesites
                            </span>
                        </div>
                    </div>

                    {/* Right card - Form */}
                    <div
                        className={`w-full lg:w-[420px] rounded-3xl p-8 backdrop-blur-xl transition-all duration-500 animate-fade-in-up ${theme === "dark"
                                ? "bg-white/5 border border-white/10 shadow-2xl shadow-purple-500/10"
                                : "bg-white/80 border border-white/50 shadow-xl"
                            }`}
                    >
                        <div className="flex items-center gap-3 mb-6">
                            <div className="w-10 h-10 rounded-xl bg-gradient-to-br from-[#C62FA8] to-[#0E6FFF] flex items-center justify-center">
                                <span className="text-lg"></span>
                            </div>
                            <h2 className={`text-xl font-bold ${theme === "dark" ? "text-white" : "text-gray-900"}`}>Registro</h2>
                        </div>

                        <form onSubmit={handleSubmit} className="space-y-4">
                            <div>
                                <label
                                    className={`block text-sm font-medium mb-2 ${theme === "dark" ? "text-gray-300" : "text-gray-700"}`}
                                >
                                    Nombre completo <span className="text-red-500">*</span>
                                </label>
                                <input
                                    type="text"
                                    value={formData.nombre}
                                    onChange={(e) => setFormData({ ...formData, nombre: e.target.value })}
                                    placeholder="Tu nombre"
                                    className={`w-full px-4 py-3 rounded-xl transition-all duration-300 focus:ring-2 focus:ring-[#C62FA8] focus:outline-none ${theme === "dark"
                                            ? "bg-white/10 border border-white/20 text-white placeholder-gray-500"
                                            : "bg-white border border-gray-200 text-gray-900 placeholder-gray-400"
                                        }`}
                                    required
                                    disabled={isLoading}
                                />
                            </div>

                            <div>
                                <label
                                    className={`block text-sm font-medium mb-2 ${theme === "dark" ? "text-gray-300" : "text-gray-700"}`}
                                >
                                    Correo electr贸nico <span className="text-red-500">*</span>
                                </label>
                                <input
                                    type="email"
                                    value={formData.email}
                                    onChange={(e) => setFormData({ ...formData, email: e.target.value })}
                                    placeholder="tu@correo.com"
                                    className={`w-full px-4 py-3 rounded-xl transition-all duration-300 focus:ring-2 focus:ring-[#C62FA8] focus:outline-none ${theme === "dark"
                                            ? "bg-white/10 border border-white/20 text-white placeholder-gray-500"
                                            : "bg-white border border-gray-200 text-gray-900 placeholder-gray-400"
                                        }`}
                                    required
                                    disabled={isLoading}
                                />
                            </div>

                            <div>
                                <label
                                    className={`block text-sm font-medium mb-2 ${theme === "dark" ? "text-gray-300" : "text-gray-700"}`}
                                >
                                    Contrase帽a <span className="text-red-500">*</span>
                                </label>
                                <div className="relative">
                                    <input
                                        type={showPassword ? "text" : "password"}
                                        value={formData.password}
                                        onChange={(e) => setFormData({ ...formData, password: e.target.value })}
                                        placeholder="⑩⑩⑩⑩⑩⑩⑩"
                                        className={`w-full px-4 py-3 pr-12 rounded-xl transition-all duration-300 focus:ring-2 focus:ring-[#C62FA8] focus:outline-none ${theme === "dark"
                                                ? "bg-white/10 border border-white/20 text-white placeholder-gray-500"
                                                : "bg-white border border-gray-200 text-gray-900 placeholder-gray-400"
                                            }`}
                                        required
                                        disabled={isLoading}
                                    />
                                    <button
                                        type="button"
                                        onClick={() => setShowPassword(!showPassword)}
                                        className={`absolute right-3 top-1/2 -translate-y-1/2 p-1 rounded-lg transition-colors ${theme === "dark" ? "text-gray-400 hover:text-white" : "text-gray-500 hover:text-gray-700"
                                            }`}
                                    >
                                        {showPassword ? <EyeOff className="w-5 h-5" /> : <Eye className="w-5 h-5" />}
                                    </button>
                                </div>
                            </div>

                            <div>
                                <label
                                    className={`block text-sm font-medium mb-2 ${theme === "dark" ? "text-gray-300" : "text-gray-700"}`}
                                >
                                    N煤mero de equipo (opcional)
                                </label>
                                <input
                                    type="text"
                                    value={formData.numeroEquipo}
                                    onChange={(e) => setFormData({ ...formData, numeroEquipo: e.target.value })}
                                    placeholder="Ej: 12"
                                    className={`w-full px-4 py-3 rounded-xl transition-all duration-300 focus:ring-2 focus:ring-[#C62FA8] focus:outline-none ${theme === "dark"
                                            ? "bg-white/10 border border-white/20 text-white placeholder-gray-500"
                                            : "bg-white border border-gray-200 text-gray-900 placeholder-gray-400"
                                        }`}
                                    disabled={isLoading}
                                />
                            </div>

                            <button
                                type="submit"
                                disabled={isLoading}
                                className="w-full py-3.5 rounded-xl font-semibold text-white transition-all duration-300 transform hover:scale-[1.02] active:scale-[0.98] disabled:opacity-70 disabled:cursor-not-allowed bg-gradient-to-r from-[#17C787] via-[#1E88E5] to-[#0E6FFF] hover:shadow-lg hover:shadow-[#1E88E5]/30 mt-2"
                            >
                                {isLoading ? (
                                    <div className="flex items-center justify-center gap-2">
                                        <div className="w-5 h-5 border-2 border-white/30 border-t-white rounded-full animate-spin" />
                                        <span>Registrando...</span>
                                    </div>
                                ) : (
                                    "Registrarse"
                                )}
                            </button>
                        </form>

                        <p className={`text-center mt-6 text-sm ${theme === "dark" ? "text-gray-400" : "text-gray-600"}`}>
                            驴Ya tienes cuenta?{" "}
                            <Link href="/login" className="text-[#1E88E5] hover:text-[#0E6FFF] font-medium transition-colors">
                                Inicia sesi贸n
                            </Link>
                        </p>
                    </div>
                </div>
            </div>
        </div>
    )
}
